'use strict';

angular.module('app.news', []).service('NewsService', ['$http', 'CONFIG', function ($http, CONFIG) {
  this.url = CONFIG.NEWS_URL;

  this.getAll = function (callback, err) {
    return $http.get(this.url).then(callback, err);
  };

  //this.getOne = function(id, callback){
  //return $http.get(this.url+id+'/').then(callback);
  //};
}]);
'use strict';

angular.module('app.news').controller('NewsCtrl', ['$scope', 'NewsService', function ($scope, NewsService) {
  $scope.news = [];
  $scope.selectedNews = {};

  $scope.loadNews = function () {
    NewsService.getAll(function (response) {
      console.log(response);
      $scope.news = response.data.map(function (item) {
        item.selected = false;
        return item;
      });
    }, function (errorResponse) {
      console.info(errorResponse);
    });
  };

  $scope.openNews = function (news) {
    news.selected = !news.selected;
    if (news.selected) {
      $scope.selectedNews = news;
    } else {
      $scope.selectedNews = {};
    }
  };
}]);
'use strict';

/*
 * Modules
 */
angular.module('app', ['ngRoute', 'ngAnimate', 'templates', 'app.news']);

/*
 * Routes
 */
angular.module('app').config(['$routeProvider', '$locationProvider', function ($routeProvider, $locationProvider) {

  $routeProvider.when("/", {
    controller: "NewsCtrl",
    templateUrl: "js/news/news.html"
  }).when("/news/:id/", {
    controller: "NewsCtrl",
    templateUrl: "js/news/news.html"
  }).otherwise({
    redirectTo: '/'
  });

  $locationProvider.html5Mode(true); //pretty urls
}]);

/*
 * Constants
 */
angular.module("app").constant("CONFIG", {
  NEWS_URL: "/data/news_mock.json"
});